*{
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.ulist li{
  display: inline;
  text-decoration: none;
  margin: 10px 20px;
}

nav{
  background: #61dafb;
  padding: 20px 0;
}

.form-switch .form-check-input{
  width: 3.5rem;
  height: 1.5rem;
  margin: 0 40px;
}
label {
  display: block;
}

.bg-light{
  border: 2px solid black;
}

.bg-dark{
  border: 2px solid white;

}

.accordian-button{
  border : 2px solid white
}